.container {
  display: flex;
  margin: 10%;
}

.column {
  flex: 1;
  min-width: 200px;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
  margin-right: 5%;
  list-style: none;

}

@keyframes shake {
  0%, 100% {
    transform: translateX(0);
  }
  25%, 75% {
    transform: translateX(-5px);
  }
  50% {
    transform: translateX(5px);
  }
}
.left-column.active {
  animation: shake 1s ease-in-out infinite;
  background-color: gray;
  
}

.right-column.active {
  animation: shake 1s ease-in-out infinite;
  background-color: gray;
}
li {
  margin: 5px 0;
  padding: 10px;
  background-color: #f4f4f4;
  border: 1px solid #ddd;
  border-radius: 5px;
  cursor: grab;
}

li.active {
  transition: transform 0.2s ease-in-out;
  transform: scale(1.05);
}
.delete-icon {
  position: absolute;
  top: 90%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-size: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.delete-icon.active {
  width: 100%;
  height: 20%;
  background-color: red;  /* active 상태에서 배경색 설정 */
  color: #fff;
}